*{
    margin: 0;
    padding: 0;
}
.App{
    background: #ffd791;
    min-height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 30px;
    transition: 1s;
}

.button{
    background: transparent;
    border-radius: 20px;
    border: solid 8px;
    cursor: pointer;
    padding: 5px;
    font-weight: 800;
    transition: 1s;
    transform: scale(.9);
    &:active{
        transform: scale(.5);
    }
}

.h1Blogs{
    background-color: #d9e8ae;
    width: 95%;
    padding: 10px;
    position: relative;
}
.btnSalir{
    position: absolute;
    font-size: 10px;
    font-weight: 700;
    width: 55px;
    height: 20px;
    right: 10px;
    top: 15px;
    padding: 5px;
    background-color: transparent;
    border-radius: 5px;
    border-width: 4px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 1s;
        &:active{
            scale: .5;
        }
}
.divBlogFormTogglable{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    row-gap: 10px;
}
.togglable{
    transition: 2s;
    min-height: 305px;
    max-width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.div2Togglable{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.Form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 3px solid #bfe3c0;
    border-radius: 10px;
    padding: 20px 40px;
    background-color: #bfe3c057;
}
.Form label{
    display: flex;
    flex-direction: column;
}
.Form label input{
    border: none;
}
.btnForm{
    border-color: #bfe3c0;
}
.btnClose{
    border-color:#c97979;
}
.btnOpen{
    border-color: #8cd676;
}

.h3Comunity{
    text-align: center;
    background-color: #ffbd87;
    width: 100%;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 20px 0;
}
.UlListBlog{
    width: 600px;
    max-height: 500px;
    margin: 5%;
    padding: 5%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 25px;
    background-color: #d9e8aea1;
    flex-wrap: wrap;
    list-style: none;
    border-radius: 20px;
    transition: 1s;
    overflow-y: scroll;
}

.LiListBlog{
    position: relative;
    text-align: center;
    max-width: 50%;
    min-width: 28%;
    min-height: 80px;
    border: 2px rgb(134, 90, 39) solid;
    border-radius: 10px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: 5px;
    padding: 10px;
}
.pLike{
    align-self: flex-end;
}

.btnDelete{
    position: absolute;
    cursor: pointer;
    background: none;
    border: none;
    right: 0;
    top: 0;
}

.btnLike{
    position: absolute;
    right: 4px;
    bottom: 35px;
    cursor: pointer;
    background: none;
    border: none;
    margin-bottom: 5px;
    font-size: 20px;
}
.btnView{
    border-color: #ffbd87;
}

.loginForm{
    padding: 30px;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 20px;
    background-color: #31251bab;
    border-radius: 10px;
    color: #ffd791;
}
.loginForm h2{
    text-shadow: 1px 1px 8px;
}
.loginDiv{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}
.loginInput{
    border: none;
    padding: 3px;
}
.loginButton{
    padding: 4px;
    font-weight: 800;
    color: #5a4c34;
    background-color: #ffd791;
    border-radius: 10px;
    border:solid 2px #2e271b;
    transition: .5s;
    cursor: pointer;
    &:active{
        scale: .8;
    }
}
.messageError{
    color: rgba(95, 18, 13, 0.801);
    border: solid 2px rgba(95, 18, 13, 0.801);
    background-color: wheat;
}